  list-style-type: disc;
  margin-bottom: 1 em;
  margin-left: 0;
  margin-right: 0;
  padding-left: 1em;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", sans-serif;
  overflow: hidden;
  border: none;
  display: flex;
  justify-content: center;
}

.Screen {
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  max-width: 1024px;
  justify-content: left;
}

.Container {
  justify-content: center;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* max-width: 414px; */
  padding-top: 5px;
  margin: 0 auto;
  height: 85vh;
  overflow-x: hidden;
  overflow-y: scroll;
  align-content: stretch;
  align-items: stretch;
}

.Container::-webkit-scrollbar {
  display: none;
}

.MyBalance {
  margin: 0px 10px;
}

.HeaderControls svg {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.Heading1 {
  width: 100vw;
  padding: 10px 0px 0px 0px;
  font-size: 1em;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

.Card {
  padding: 15px;
  position: relative;
  width: 100%;
  line-height: 1.35em;
}

.CardClose {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-light);
  cursor: pointer;
}

.CategoryCardIcon {
  width: 85px;
  height: 85px;
  opacity: 1;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  margin-bottom: 7px;
  position: absolute;
  top: 20px;
  right: -35px;
}

.CategoryCardIcon svg {
  -webkit-transform: scale(2.5) rotate(0deg);
          transform: scale(2.5) rotate(0deg);
  opacity: 0.15;
  color: white;
}

.PageCardContainer {
  position: fixed;
  top: 120vh;
  min-height: 100vh;
  transition: 0.2s ease-in-out;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.PageCard {
  border-radius: 20px 20px 0 0;
  border: 1px solid rgba(0, 0, 0, 0.05);
  /* box-shadow: 0px 0px 40px 1px rgba(0, 0, 0, 0.3); */
  height: 100vh;
  overflow: scroll;
  width: auto;
}

.PageCard h1 {
  font-size: 1.4em;
}

.PageCardContent {
  padding: 20px;
}

.pageClose {
  width: 35px;
  height: 35px;
  border-radius: 20px;
  background: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 15px;
  right: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  cursor: pointer;
}

.PageCloseBg {
  width: 100%;
  height: 10vh;
  background: rgba(255, 255, 255, 0.3);
  position: absolute;
  top: -5vh;
  left: 0px;
  z-index: -1;
}

.PageIcon svg {
  -webkit-transform: scale(7) rotate(0deg);
          transform: scale(7) rotate(0deg);
}

.MuiFormControlLabel-root {
  padding-bottom: 10px;
}

/* Styles for scrollbar */

.slides::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

.slides::-webkit-scrollbar-thumb {
  background: #007AFF;
  border-radius: 5px;
}

.slides::-webkit-scrollbar-track {
  background: #DDD;
}

@media (min-width: 1024px) {
  .MuiModal-root .MuiBackdrop-root  {
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
  }
}

.focus\:border-transparent:focus {
  border-color: transparent;
}

.focus\:outline-none:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}

.focus\:ring-0:focus {
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
  --tw-ring-shadow: var(--tw-ring-inset) 0 0 0 calc(0px + var(--tw-ring-offset-width)) var(--tw-ring-color);
  box-shadow: var(--tw-ring-offset-shadow), var(--tw-ring-shadow), var(--tw-shadow, 0 0 #0000);
}

.dark .dark\:border-0 {
  border-width: 0px;
}

.dark .dark\:bg-slate-800 {
  --tw-bg-opacity: 1;
  background-color: rgb(30 41 59 / var(--tw-bg-opacity));
}

.dark .dark\:bg-black {
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity));
}

.dark .dark\:bg-slate-900 {
  --tw-bg-opacity: 1;
  background-color: rgb(15 23 42 / var(--tw-bg-opacity));
}

.dark .dark\:bg-opacity-25 {
  --tw-bg-opacity: 0.25;
}

.dark .dark\:bg-opacity-5 {
  --tw-bg-opacity: 0.05;
}

.dark .dark\:bg-opacity-20 {
  --tw-bg-opacity: 0.2;
}

.dark .dark\:bg-opacity-10 {
  --tw-bg-opacity: 0.1;
}

.dark .dark\:from-slate-900 {
  --tw-gradient-from: #0f172a;
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to, rgb(15 23 42 / 0));
}

.dark .dark\:via-slate-900 {
  --tw-gradient-stops: var(--tw-gradient-from), #0f172a, var(--tw-gradient-to, rgb(15 23 42 / 0));
}

.dark .dark\:text-gray-50 {
  --tw-text-opacity: 1;
  color: rgb(249 250 251 / var(--tw-text-opacity));
}

.dark .dark\:text-blue-200 {
  --tw-text-opacity: 1;
  color: rgb(191 219 254 / var(--tw-text-opacity));
}

.dark .dark\:text-slate-100 {
  --tw-text-opacity: 1;
  color: rgb(241 245 249 / var(--tw-text-opacity));
}

.dark .dark\:shadow-none {
  --tw-shadow: 0 0 #0000;
  --tw-shadow-colored: 0 0 #0000;
  box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
}

@media (min-width: 1024px) {
  .lg\:mt-4 {
    margin-top: 1rem;
  }
}